import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {AngularFireAuth, AngularFireAuthModule} from "angularfire2/auth";
import {AngularFireModule} from 'angularfire2';
import {Facebook} from "@ionic-native/facebook";
import * as firebase from 'firebase';

/**
 * Generated class for the CreateAccountPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-create-account',
  templateUrl: 'create-account.html',
})
export class CreateAccountPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private authdb: AngularFireAuth, private facebook: Facebook) {
  }


  createWithFacebook(): Promise<any> {
        return this.facebook.login(['email'])
            .then( response => {
            const facebookCredential = firebase.auth.FacebookAuthProvider
                .credential(response.authResponse.accessToken);
console.log(facebookCredential);
                this.authdb.auth.signInWithCredential(facebookCredential)
                .then( success => {
                    console.log("Firebase success: " + JSON.stringify(success));
                });

        }).catch((error) => { console.log(error) });
  }

}



